@import "../variables";
@import "../mixins";

.popup {
    &__container {
        @include flex-column;
        align-items: center;
    }

    &__title {
        @include reset;
        @include text-format(700, 26px, 34px);
        color: $blue;
    }

    &__paragraph {
        @include reset;
        margin-top: 10px;
        @include text-format(400, 18px, 28px);
        color: #414141;
    }

    &__subtitle {
        margin: 9px 0 27px 0;
        @include text-format(400, 22px, 28px);
        color: #414141;
    }

    &__text {
        margin: 16px 0 0;
        @include text-format(700, 26px, 34px);
        color: #414141;
    }

    &__authorsImage {
        width: 600px;
        min-height: 428px;
    }

    &__imageContainer {
        position: relative;
    }

    &__payImage {
        width: 500px;
        min-height: 500px;
    }

    &__payValue {
        @include reset;
        @include text-format(700, 22px, 16px);
        position: absolute;
        top: 140px;
        left: 190px;
    }

    &__bottom {
        @include flex-center-between;
        gap: 15px;
        margin-top: 25px;
    }


    &__name {
        text-decoration: none;
        color: $mainBlack;
        span {
            color: $mainBlack;
        }
    }

    &_none {
        display: flex;
    }
}