@import "../../variables";
@import "../../mixins";


.order {
    @include flex-column;
    @include flex-between;
    align-self: flex-start;
    gap: 42px;
    background-color: #FBFBFB;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.03), 0 2px 2px 0px rgba(0, 0, 0, 0.06), 0 1px 5px 0px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    padding: 20px;
    min-width: 380px;
    box-sizing: border-box;
}

.wrapper {
    @include flex-center;
    gap: 35px;
}

.form {
    background-color: #FBFBFB;
    width: 100%;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.03), 0 2px 2px 0px rgba(0, 0, 0, 0.06), 0 1px 5px 0px rgba(0, 0, 0, 0.12);

    &__wrapper {
        @include flex-column;
        gap: 25px;
    }

    &__box {
        display: flex;
        gap: 30px;
    }

    &__title {
        @include reset;
        margin-bottom: 15px;
        @include text-format(700, 18px, 23px);
        color: #414141;
    }
}